// Variables

//Mixins

@mixin con-aside{
  display: grid;
  margin: auto;
  max-width: var(--main-maximo);
  grid-gap: var(--gap-aside);
  grid-template-columns: var(--columns-aside);

}


.Container-central{
  border: 4px solid red;
  width: var(--main-maximo);
  max-width: $ancho-maximo;
  margin: auto;
}
